.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  padding: 10px;

}

.CardGrid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(10, auto);
}

.Info {
  flex: 1
}

.ColNames {
  padding: 10px 0;
}

.Cell {
  /*background-color: rgba(255, 255, 255, 0.8);*/
  border: 1px solid rgba(0, 0, 0, 0.8);
  align-items: center;
  justify-content: center;
  display: flex;
}

.Item {
  flex-direction: column;
}

.Item > div {
  display: flex;
  flex: 1;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.Item > div {
  cursor: pointer;
}

.Item > div:first-child {
  border-bottom: solid 1px #8d8d8d;
}

.OppSelect {
  margin-left: 20px;
}

.SelectedItem {
  box-shadow: 1px 1px 6px 1px #0046ff inset;
}

.SelectedInfo {
  margin-top: 40px;
  margin-left: 20px;
}

.PredictResultGrid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(3, auto);
  margin-top: 40px;
}

.PredictResultGrid > div {
  border: 1px solid rgba(0, 0, 0, 0.8);
  align-items: center;
  justify-content: center;
  display: flex;
}

.PredictRanksGrid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(10, auto);
  margin-top: 40px;
}

.PredictRanksGrid > div {
  border: 1px solid rgba(0, 0, 0, 0.8);
  align-items: center;
  justify-content: center;
  display: flex;
}
